import React, { useState } from 'react'
import styles from './TopSidebar.module.scss'
import { NavLink } from 'react-router-dom'
import config from '../../../config'
import { AiFillHome, AiOutlineUsergroupDelete } from 'react-icons/ai'
import { BsCameraVideo } from 'react-icons/bs'
import Button from '../../common/Button'
import Modal from 'react-modal'
import Login from '../../Login'
import { useDispatch, useSelector } from 'react-redux'
import { loginModal, registerModal, hideModal } from '../../../redux/actions'
import Register from '../../../components/Register'

const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)'
    }
}
const LOGIN_MODAL = 'LOGIN_MODAL'
const REGISTER_MODAL = 'REGISTER_MODAL'
const token = window.localStorage.getItem('token')
const TopSidebar = ({ desc = '' }) => {
    const MODAL = useSelector(state => state.modal.modalLogin)
    const modalRegister = useSelector(state => state.modal.modalRegister)
    const dispatch = useDispatch()

    const handleShowModalRegister = () => {
        const action = registerModal()
        dispatch(action)
    }

    const handleShowModalLogin = () => {
        const action = loginModal()
        dispatch(action)
    }

    const closeModal = () => {
        const action = hideModal()
        dispatch(action)
    }

    return (
        <div className={styles.wrapper}>
            <NavLink
                className={styles.navItem}
                to={config.routes.home}
                activeStyle={{
                    fontWeight: '700',
                    color: '#FE2C55'
                }}
            >
                <div className={styles.iconWrap}>
                    <AiFillHome className={styles.icon} />
                    <span>Danh cho ban</span>
                </div>
            </NavLink>
            <NavLink
                className={styles.navItem}
                to={`${config.routes.follow}`}
                activeStyle={{
                    fontWeight: '700',
                    color: '#161823'
                }}
            >
                <div className={styles.iconWrap}>
                    <AiOutlineUsergroupDelete className={styles.icon} />
                    <span>ang Follow</span>
                </div>
            </NavLink>
            {token ? (
                <NavLink
                    className={styles.navItem}
                    to={config.routes.home}
                    activeStyle={{
                        fontWeight: '700',
                        color: '#161823'
                    }}
                >
                    <div className={styles.iconWrap}>
                        <BsCameraVideo className={styles.icon} />
                        <span>Video</span>
                    </div>
                </NavLink>
            ) : (
                <></>
            )}
            <div className={styles.loginWrapper}>
                <span className={styles.desc}>{desc}</span>
                {token ? (
                    <></>
                ) : (
                    <Button
                        children="ng nhp"
                        border
                        type="default"
                        size="l"
                        color="colorPrimary"
                        marginTop
                        hoverPrimaryColor
                        onClick={handleShowModalLogin}
                    />
                )}
            </div>
            <Modal isOpen={MODAL === LOGIN_MODAL} style={customStyles}>
                <Login
                    onShowModalLogin={handleShowModalRegister}
                    onCloseModal={closeModal}
                    title="Logout to Tiktok"
                    accountTitle="Ban a co tai khoan"
                    loginText="ng nhp"
                />
            </Modal>
            <Modal isOpen={modalRegister === REGISTER_MODAL} style={customStyles}>
                <Register
                    title="Login to Tiktok"
                    accountTitle="Ban khng co tai khoan"
                    loginText="ng ky"
                    onShowModalRegister={handleShowModalLogin}
                    onCloseModal={closeModal}
                />
            </Modal>
        </div>
    )
}

export default TopSidebar
